/* Footer General Styles */
footer {
    background: var(--color-primary);
    padding: 3rem 2rem;
    text-align: center;
    color: var(--color-bg);
    font-size: 1rem;
    margin-top: 7rem;
    box-shadow: 0 -4px 6px rgba(0, 0, 0, 0.3);
    /* Subtle top shadow */
}

/* Footer Logo */
.footer__logo {
    font-size: 2.5rem;
    /* Increased size */
    font-weight: 700;
    color: var(--color-bg);
    text-decoration: none;
    margin-bottom: 2rem;
    display: inline-block;
    transition: transform 0.3s ease-in-out, color 0.3s ease-in-out;
}

.footer__logo:hover {
    color: var(--color-bg-variant);
    /* Hover effect */
    transform: scale(1.1);
}

/* Footer Links (Permalinks) */
.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin-bottom: 2.5rem;
}

.permalinks a {
    color: var(--color-bg);
    font-weight: 600;
    text-decoration: none;
    transition: color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.permalinks a:hover {
    color: var(--color-bg-variant);
    /* Lighter color on hover */
    transform: scale(1.05);
}

/* Footer Social Links */
.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 2rem;
}

.footer__socials a {
    color: var(--color-bg);
    font-size: 1.5rem;
    /* Icon size */
    transition: color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.footer__socials a:hover {
    color: var(--color-bg-variant);
    transform: scale(1.2);
    /* Slight scale-up effect */
}

/* Footer Copyright */
.footer__copyright {
    font-size: 0.9rem;
    color: var(--color-bg-variant);
    margin-top: 1rem;
}

/* Responsive Design */
@media screen and (max-width: 1024px) {
    .footer__logo {
        font-size: 2rem;
    }

    .permalinks {
        gap: 1.5rem;
    }

    .footer__socials a {
        font-size: 1.3rem;
    }
}

@media screen and (max-width: 600px) {
    .footer__logo {
        font-size: 1.8rem;
    }

    .permalinks {
        flex-direction: column;
        gap: 1.2rem;
    }

    .footer__socials {
        gap: 1rem;
    }

    .footer__socials a {
        font-size: 1.2rem;
    }

    .footer__copyright {
        font-size: 0.8rem;
    }
}