/* Reset margins and padding */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header {
    height: 100vh;
    /* Full viewport height */
    display: flex;
    align-items: center;
    /* Center vertically */
    justify-content: center;
    /* Center horizontally */
    position: relative;
    /* Needed for gradient positioning */
    overflow: hidden;
    background-color: #0d0f16;
    /* Fallback background */
}

.header_container {
    text-align: center;
    position: relative;
    z-index: 1;
    /* Keep header content on top */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    color: white;
    /* Ensure text is visible */
}

/* Adjust Font Sizes */
.header_container h5 {
    font-size: 1.8rem;
    /* Increase size of "Hello I'm" */
    font-weight: 400;
    color: var(--color-light);
}

.header_container h1 {
    font-size: 4rem;
    /* Larger size for your name */
    font-weight: 700;
    color: white;
}

.header_container h5.text-light {
    font-size: 1.5rem;
    /* Increase size for "Fullstack Java Developer" */
    font-weight: 300;
    color: var(--color-primary);
}

/* CTA Buttons */
.cta {
    margin-top: 1.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.cta a {
    font-size: 1.2rem;
    /* Button text size */
    padding: 0.8rem 1.5rem;
    text-decoration: none;
    /* Remove underline */
    color: #ffffff;
    /* Explicit white text color */
    background-color: var(--color-primary);
    /* Consistent background color */
    border: none;
    /* Remove any default borders */
    border-radius: 0.5rem;
    /* Rounded edges */
    font-weight: 600;
    text-align: center;
    transition: all 0.3s ease-in-out;
}

.cta a:hover {
    background-color: var(--color-primary-variant);
    /* Slightly different color on hover */
    transform: scale(1.05);
    /* Smooth scaling effect */
}

/* Scroll Down */
.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 4rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    cursor: pointer;
}

/* Moving Stars Background */
.stars {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 0;
    pointer-events: none;
}

.star {
    position: absolute;
    background-color: white;
    border-radius: 50%;
    opacity: 0.8;
    animation: twinkle 5s infinite ease-in-out, moveStar 10s infinite linear;
}

@keyframes twinkle {

    0%,
    100% {
        opacity: 0.3;
        transform: scale(1);
    }

    50% {
        opacity: 1;
        transform: scale(1.5);
    }
}

@keyframes moveStar {
    from {
        transform: translateY(-10vh);
    }

    to {
        transform: translateY(110vh);
    }
}
.header__socials {
    display: flex;
    flex-direction: column;
    /* Align vertically */
    align-items: center;
    /* Center horizontally */
    gap: 1.5rem;
    /* Add spacing between LinkedIn and GitHub */
    position: absolute;
    left: 0;
    bottom: 2rem;
}

.header__socials a {
    color: var(--color-light);
    /* Ensure icons are visible */
    font-size: 1.5rem;
    /* Adjust icon size if needed */
    transition: color 0.3s ease;
    /* Smooth color transition */
}

.header__socials a:hover {
    color: var(--color-primary);
    /* Highlight icons on hover */
}
/* Media Queries for Small Devices */
@media screen and (max-width: 1024px) {
    .header_container h1 {
        font-size: 3rem;
        /* Smaller size for tablets */
    }

    .header_container h5 {
        font-size: 1.5rem;
        /* Adjust "Hello I'm" */
    }

    .header_container h5.text-light {
        font-size: 1.2rem;
        /* Adjust job title */
    }
}

@media screen and (max-width: 600px) {
    .floating-gradient {
        height: 150px;
        /* Smaller gradient */
        filter: blur(80px);
    }

    .header_container h1 {
        font-size: 2.5rem;
        /* Adjust for mobile devices */
    }

    .header_container h5 {
        font-size: 1.2rem;
    }

    .header_container h5.text-light {
        font-size: 1rem;
    }
}