/* General Experience Section Styling */
#experience {
    padding: 3rem 2rem;
    background: #0d0f16;
    /* Dark background for contrast */
    color: #ffffff;
    text-align: center;
}

#experience h2 {
    font-size: 2.2rem;
    color: var(--color-primary);
    margin-bottom: 2rem;
}

/* Container Grid */
.experience__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    /* Responsive grid */
    gap: 2rem;
    justify-content: center;
}

/* Experience Card */
.experience {
    background: #1b1e2e;
    border-radius: 1rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    overflow: hidden;
    transition: all 0.3s ease-in-out;
    position: relative;
}

.experience:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.5);
    background: #242840;
    /* Slight background change */
}

/* Experience Header */
.experience__head {
    background: var(--color-primary);
    padding: 1.5rem 1rem;
    text-align: center;
    border-bottom: 1px solid var(--color-primary-variant);
}

.experience__head h3 {
    color: #ffffff;
    font-size: 1.2rem;
    font-weight: bold;
    text-transform: uppercase;
}

/* Experience List */
.experience__list {
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.experience__list li {
    display: flex;
    align-items: flex-start;
    gap: 0.75rem;
    margin-bottom: 0.5rem;
    transition: all 0.3s ease;
}

.experience__list li:hover {
    transform: translateX(10px);
    color: var(--color-primary);
    /* Highlight text on hover */
}

.experience__list-icon {
    color: var(--color-primary);
    font-size: 1.2rem;
    flex-shrink: 0;
}

.experience__list p {
    font-size: 1rem;
    line-height: 1.5;
    margin: 0;
}

/* Responsive Adjustments */
@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        gap: 1.5rem;
    }

    .experience {
        padding: 1rem;
    }
}

@media screen and (max-width: 600px) {
    .experience__container {
        grid-template-columns: 1fr;
        /* Single column for small devices */
    }

    .experience__head h3 {
        font-size: 1rem;
    }

    .experience__list p {
        font-size: 0.9rem;
    }
}