/* General Contact Section Styling */
#contact {
    padding: 3rem 2rem;
    background-color: #0d0f16;
    /* Matching dark background */
    color: #ffffff;
    text-align: center;
}

#contact h5 {
    font-size: 1.2rem;
    color: var(--color-light);
    margin-bottom: 0.5rem;
}

#contact h2 {
    font-size: 2.5rem;
    color: var(--color-primary);
    margin-bottom: 2rem;
}

/* Contact Container */
.container.contact__container {
    width: 60%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
    align-items: center;
}

/* Contact Options */
.contact__options {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.contact__option {
    background: #1b1e2e;
    padding: 1.5rem;
    border-radius: 1rem;
    text-align: center;
    border: 1px solid transparent;
    transition: all 0.3s ease-in-out;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

.contact__option:hover {
    background: #242840;
    transform: translateY(-5px);
    border-color: var(--color-primary);
}

.contact__option-icon {
    font-size: 2rem;
    color: var(--color-primary);
    margin-bottom: 1rem;
}

.contact__option h4 {
    font-size: 1.1rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
}

.contact__option h5 {
    font-size: 0.9rem;
    color: var(--color-light);
}

.contact__option a {
    display: inline-block;
    margin-top: 0.7rem;
    color: var(--color-primary);
    text-decoration: none;
    font-size: 0.9rem;
    transition: color 0.3s ease;
}

.contact__option a:hover {
    color: var(--color-primary-variant);
    text-decoration: underline;
}

/* Form Styling */
form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    background: #1b1e2e;
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

input,
textarea {
    width: 100%;
    padding: 1rem;
    border-radius: 0.5rem;
    background: #131623;
    border: 2px solid var(--color-primary);
    color: #ffffff;
    font-size: 1rem;
    transition: border-color 0.3s ease-in-out;
}

input:focus,
textarea:focus {
    border-color: var(--color-primary-variant);
    outline: none;
}

button {
    padding: 0.75rem 1.5rem;
    border: none;
    background: var(--color-primary);
    color: #ffffff;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

button:hover {
    background: var(--color-primary-variant);
    transform: translateY(-3px);
}

/* Responsive Adjustments */
@media screen and (max-width: 1024px) {
    .container.contact__container {
        grid-template-columns: 1fr;
        width: 80%;
        gap: 2rem;
    }

    form {
        padding: 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .container.contact__container {
        width: 100%;
        padding: 0 1rem;
    }

    form {
        padding: 1rem;
    }

    input,
    textarea {
        font-size: 0.9rem;
    }
}