/* General About Section Styling */
#about {
    padding: 4rem 2rem;
    background-color: #0d0f16;
    /* Dark background */
    color: #ffffff;
    text-align: center;
}

#about h5 {
    font-size: 1.5rem;
    /* Increased size for "Get To Know" */
    font-weight: 600;
    color: var(--color-primary);
    margin-bottom: 0.5rem;
}

#about h2 {
    font-size: 3rem;
    /* Larger font size for "About Me" */
    font-weight: 700;
    margin-bottom: 2.5rem;
    color: var(--color-white);
}

/* About Container */
.about__container {
    display: grid;
    grid-template-columns: 40% 55%;
    /* Adjust column widths */
    gap: 3rem;
    align-items: center;
    /* Center align vertically */
    justify-content: center;
    margin: 0 auto;
    max-width: 1200px;
    /* Centered and constrained container */
}

/* Image Container with Gradient Background */
.about__me {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 2rem;
    background: linear-gradient(135deg,
            transparent,
            var(--color-primary),
            transparent);
    width: 100%;
    max-width: 350px;
    aspect-ratio: 1/1;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    transition: transform 0.3s ease-in-out;
}

.about__me-image {
    width: 90%;
    height: 90%;
    border-radius: 2rem;
    object-fit: cover;
    /* Ensures the image fits perfectly */
    transform: rotate(45deg);
    /* Tilts the image */
    transition: transform 0.3s ease-in-out;
}

.about__me:hover .about__me-image {
    transform: rotate(0deg) scale(1.05);
    /* Image straightens and scales */
}

.about__me:hover {
    transform: scale(1.05);
    /* Background card scales up */
}

/* About Content */
.about__content {
    text-align: left;
    line-height: 1.6;
}

.about__content p {
    font-size: 1.1rem;
    /* Slightly larger paragraph text */
    margin-bottom: 2rem;
    color: var(--color-light);
}

/* About Cards */
.about__cards {
    display: flex;
    gap: 2rem;
    justify-content: center;
    margin-bottom: 1.5rem;
}

.about__card {
    background: #1b1e2e;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1.5rem;
    text-align: center;
    transition: all 0.3s ease-in-out;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

.about__card:hover {
    background: #242840;
    transform: translateY(-5px);
    border-color: var(--color-primary);
}

.about__icon {
    font-size: 2rem;
    /* Larger icon size */
    color: var(--color-primary);
    margin-bottom: 0.5rem;
}

.about__card h5 {
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 0.3rem;
    color: var(--color-light);
}

.about__card small {
    font-size: 1rem;
    color: #bdbdbd;
}

/* Button */
.btn {
    display: inline-block;
    padding: 0.8rem 1.5rem;
    background: var(--color-primary);
    color: #ffffff;
    font-size: 1rem;
    border-radius: 0.5rem;
    transition: all 0.3s ease-in-out;
    text-decoration: none;
}

.btn:hover {
    background: var(--color-primary-variant);
    transform: scale(1.05);
}

/* Responsive Design */
@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        /* Single column layout */
        gap: 2rem;
    }

    .about__me {
        max-width: 300px;
    }

    .about__cards {
        flex-direction: column;
        gap: 1rem;
    }

    #about h2 {
        font-size: 2.5rem;
    }
}

@media screen and (max-width: 600px) {
    .about__container {
        grid-template-columns: 1fr;
    }

    .about__me {
        max-width: 250px;
    }

    .about__content p {
        font-size: 1rem;
    }

    .about__icon {
        font-size: 1.5rem;
    }

    #about h2 {
        font-size: 2rem;
    }
}